import { createWebHistory,createRouter } from "vue-router";
import Home from '@/Home.vue'
import Yoo from '@/Yoo.vue'
import Archives  from '@/Archives.vue'
import Foundation from '@/Foundation.vue'


const router = createRouter({
  history : createWebHistory(),
  routes : [
    {path:"/yk", name:"Home",component:Home},
    {path:"/Yoo",name:"Yoo",component:Yoo},
    {path:"/Archives",name:"Archives",component:Archives},
    {path:"/Foundation",name:"Foundation",component:Foundation},
  ]
});
export default router;